.back-drop{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.25);
    opacity: 1;
    transition: opacity 250ms $primary-animation;
    overflow-y: scroll;
    padding: 40px 20px 20px 20px;
    
  
    &.is-hidden {
      opacity: 0;
      pointer-events: none;
    }
}
.modal{
  position: relative;
  top: 67.5%;
  left: 50%;
  background-color: $lite-text-color;
  padding: 48px 20px 20px 20px;
  transform: translate(-50%, -50%);
  min-width: 280px;
  max-width: 480px;

  @media screen and (min-width: 768px) {
    min-width: 768px;
    top: 50%;
  }
  @media screen and (min-width: 1024px) {
    max-width: 882px;
  }
  &_close{
    position: absolute;
    top:10px;
    right: 10px;
    background-color: transparent;
      display: flex;
      padding: 0;
      border: none;
      width: 30px;
      height: 30px;
      margin-bottom: 8px;
  }
  &_close-icon{
    max-height: 30px;
  }
  &-button-container{
    display: flex ;
  }
  &-button{
    min-width: 119px;
    padding: 14px 0;
  }

}